import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class ContactScreen extends Component {

    render() {
        return (
            <div className="">
                <nav class="navbar navbar-b navbar-trans navbar-expand-md" style={{backgroundColor: '#000000'}} id="mainNav">
                    <div class="container">
                    <Link class="navbar-brand" to="/">CodeForDevApps</Link>
                    <button class="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarDefault"
                        aria-controls="navbarDefault" aria-expanded="false" aria-label="Toggle navigation">
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                    <div class="navbar-collapse collapse justify-content-end" id="navbarDefault">
                        <ul class="navbar-nav">
                        <li class="nav-item">
                            <Link class="nav-link" to="/">React Native</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="/React-js">React js</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="/Contact">Contact</Link>
                        </li>
                        </ul>
                    </div>
                    </div>
                </nav> 
                <div id="home" class="intro" style={{backgroundColor: 'black'}}>
                    <div class="overlay-itro"></div>
                    <div class="intro-content display-table">
                    <div class="table-cell">
                        <div class="container">
                            <h2 class="intro-title mb-4">CodeForDevApps Contact</h2>
                            <p class="intro-subtitle"><span class="">Share your idea's to CodeForDevApps</span></p>
                        </div>
                    </div>
                    </div>
                </div>
                <div style={{marginTop: '30px'}}>
                    <div class="container">
                        <div class="row">
                            <div class="col-sm-4"/>
                            <div class="col-sm-4">
                                <div class="service-box">
                                    <center>
                                        <div class="service-ico">
                                            <img src={require('../image/vijay250.jpg')} class="rounded-circle" style={{}} alt=""/>
                                        </div>
                                    </center>
                                    <div class="service-content">
                                        <h1 class="s-title">Vijayakumar KTM</h1>
                                        <h5 class="" style={{textAlign: 'center'}}>Founder</h5>
                                        <h6 class="" style={{textAlign: 'center'}}>mathivijay18@gmail.com</h6>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-4"/>
                        </div>
                    </div>
                </div>
                <div style={{marginTop: '30px'}}>
                    <div style={{backgroundColor: 'black', textAlign: 'center', color: 'white', padding: '20px'}}>
                        <span style={{}}>
                            Created by CodeForDevApps
                        </span>
                    </div>
                </div>
            </div>
          );
    }
}

export default ContactScreen;