import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// import Prism from 'prismjs';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { prism } from 'react-syntax-highlighter/dist/esm/styles/prism';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class DetailReactjsScreen extends Component {

    constructor(props){
        super(props);
        this.state={
            tabStatus: 1,
        };
    }

    componentDidMount(){
        // setTimeout(() => Prism.highlightAll(), 0)
    }

    render() {
        const state = this.props.location.state.imagePath;
        const title = this.props.location.state.title;
        const code = this.props.location.state.code;
        const css = this.props.location.state.css;
        const description = this.props.location.state.description;
        return (
            <div className="">
                <nav class="navbar navbar-b navbar-trans navbar-expand-md" style={{backgroundColor: '#000000'}} id="mainNav">
                    <div class="container">
                    <Link class="navbar-brand" to="/">CodeForDevApps</Link>
                    <button class="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarDefault"
                        aria-controls="navbarDefault" aria-expanded="false" aria-label="Toggle navigation">
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                    <div class="navbar-collapse collapse justify-content-end" id="navbarDefault">
                        <ul class="navbar-nav">
                        <li class="nav-item">
                            <Link class="nav-link" to="/">React Native</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="./React-js">React js</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="/Contact">Contact</Link>
                        </li>
                        </ul>
                    </div>
                    </div>
                </nav> 
                <div style={{textAlign: 'center', color: 'black', marginTop: '30px'}}><h2>{title}</h2></div>
                <div style={{marginTop: '30px'}}>
                    <div class="container">
                        <div class="row">
                            <div class="col-sm-4" style={{marginTop: '15px'}}>
                                {state &&
                                    <img className="img-fluid"  style={{border: '2px solid black'}} src={state} alt=""/>
                                }
                            </div>
                            <div class="col-sm-8"  style={{marginTop: '25px', overflow:'scroll', overflowX:'hidden', overflowY: 'hidden'}}>
                                {/* <div className="row">
                                    <div class= "col-sm-1"/>
                                    <div class="col-sm-3" onClick={()=>this.setState({tabStatus: 1})} style={this.state.tabStatus === 1? {textAlign: 'center', paddingBottom: '15px', borderBottom:'solid 4px #000000'} : {textAlign: 'center'}}>
                                        <span className="">App.js</span>
                                    </div>
                                    <div class="col-sm-3"  onClick={()=>this.setState({tabStatus: 2})} style={this.state.tabStatus === 2? {textAlign: 'center', paddingBottom: '15px', borderBottom:'solid 4px #000000'} : {textAlign: 'center'}}>
                                        <span className="">App.css</span>
                                    </div>
                                    <div class="col-sm-3"  onClick={()=>this.setState({tabStatus: 3})} style={this.state.tabStatus === 3? {textAlign: 'center', paddingBottom: '15px', borderBottom:'solid 4px #000000'} : {textAlign: 'center'}}>
                                        <span className="">Description</span>
                                    </div>
                                </div> */}
                                <ul className="nav-ul">
                                    <li className="nav-li" onClick={()=>this.setState({tabStatus: 1})} style={this.state.tabStatus === 1 ? {borderBottom: 'solid 4px #000000'}: {}}><span className="nav-li-a">App.js</span></li>
                                    <li className="nav-li" onClick={()=>this.setState({tabStatus: 2})} style={this.state.tabStatus === 2 ? {borderBottom: 'solid 4px #000000'}: {}}><span className="nav-li-a">App.css</span></li>
                                    <li className="nav-li" onClick={()=>this.setState({tabStatus: 3})} style={this.state.tabStatus === 3 ? {borderBottom: 'solid 4px #000000'}: {}}><span className="nav-li-a">Description</span></li>
                                </ul>
                                <div style={{paddingLeft: '10px', marginTop: '30px'}}>
                                    {/* <pre className="line-numbers">
                                        <code className="language-js">{code}</code>
                                    </pre> */}
                                    <div className="row">
                                        <div class="col-sm-9"/>
                                        <div class="col-sm-2" style={{marginLeft: '10px', paddingBottom: '15px'}}>
                                            <CopyToClipboard text={this.state.tabStatus === 1 ? code : this.state.tabStatus === 2 ? css : description}
                                                onCopy={() => this.setState({copied: true})}>
                                                <span className="copy-button">Copy</span>
                                            </CopyToClipboard>
                                        </div>
                                    </div>
                                    <div style={this.state.tabStatus === 1 ? {} : this.state.tabStatus === 2 ? {} : {height: 600}}>
                                        <SyntaxHighlighter language="javascript" style={prism}>
                                            {this.state.tabStatus === 1 ? code : this.state.tabStatus === 2 ? css : description}
                                        </SyntaxHighlighter>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div style={{marginTop: '30px'}}>
                    <div style={{backgroundColor: 'black', textAlign: 'center', color: 'white', padding: '20px'}}>
                        <span style={{}}>
                            Created by CodeForDevApps
                        </span>
                    </div>
                </div>
            </div>
          );
    }
}

export default DetailReactjsScreen;