import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { loginScreen1, loginScreen1Css, loginScreen1Description } from '../utils/JsCodedetails';

class MainReactjsScreen extends Component {

    render() {
        return (
            <div className="">
                <nav class="navbar navbar-b navbar-trans navbar-expand-md" style={{backgroundColor: '#000000'}} id="mainNav">
                    <div class="container">
                    <Link class="navbar-brand" to="/">CodeForDevApps</Link>
                    <button class="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarDefault"
                        aria-controls="navbarDefault" aria-expanded="false" aria-label="Toggle navigation">
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                    <div class="navbar-collapse collapse justify-content-end" id="navbarDefault">
                        <ul class="navbar-nav">
                        <li class="nav-item">
                            <Link class="nav-link" to="/">React Native</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="./React-js">React js</Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="/Contact">Contact</Link>
                        </li>
                        </ul>
                    </div>
                    </div>
                </nav> 
                <div id="home" class="intro" style={{backgroundColor: 'black'}}>
                    <div class="overlay-itro"></div>
                    <div class="intro-content display-table">
                    <div class="table-cell">
                        <div class="container">
                            <h1 class="intro-title mb-4">React js Source code</h1>
                            <p class="intro-subtitle"><span class="">For Web Application Developer</span></p>
                        </div>
                    </div>
                    </div>
                </div>
                <div style={{marginTop: '30px', marginBottom: '50px'}}>
                    <div class="container">
                        <div class="row">
                            <div class="col-sm-3">
                                
                            </div>
                            <div class="col-sm-6">
                                <Link class="nav-link" to={{
                                    pathname: '/Reactjs-code',
                                    state: {
                                        imagePath: require('../image/reactjsLoginScreen.PNG'),
                                        title: 'Login Screen Design',
                                        code: loginScreen1,
                                        css: loginScreen1Css,
                                        description: loginScreen1Description
                                    }
                                }}>
                                    <img className="" style={{border: '2px solid black'}} src={require('../image/reactjsLoginScreen.PNG')} width={'100%'} height={250} alt=""/>
                                </Link>
                            </div>
                            <div class="col-sm-3">
                                
                            </div>
                        </div>
                    </div>
                </div>
                <div style={{marginTop: '30px'}}>
                    <div style={{backgroundColor: 'black', textAlign: 'center', color: 'white', padding: '20px'}}>
                        <span style={{}}>
                            Created by CodeForDevApps
                        </span>
                    </div>
                </div>
            </div>
          );
    }
}

export default MainReactjsScreen;